.callback-form {
  width: 100%;
  max-width: 570px;
}

.callback-form__inputs {
  flex-direction: column;
  gap: 20px;
  display: flex;
}

.callback-form__button {
  width: 100%;
  margin-top: 20px;
}

.callback-form__button.button {
  backdrop-filter: blur();
  height: 60px;
  padding: 24px;
  font-weight: 400;
}

.callback-form__input {
  width: 100%;
}

@media (width <= 768px) {
  .callback-form {
    max-width: 100%;
    margin-top: 60px;
  }
}
